}
h2 {
  margin-top: 0;
  font-size: 1.75em;
}
h3 {
  font-size: 2rem;
}

ul {
  list-style-type: disc;
  list-style-position: inside;
}

.arrow #arrow-head {
	-webkit-transform-origin: right;
	        transform-origin: right;
	-webkit-transform: scale(1, 1);
	        transform: scale(1, 1);
	transition: all 200ms ease;
}
.arrow:hover #arrow-head {
	-webkit-transform: scale(1, 0.9);
	        transform: scale(1, 0.9);
	transition: all 200ms ease;
}
.arrow #arrow-body {
	-webkit-transform-origin: right;
	        transform-origin: right;
	-webkit-transform: scale(0.9, 1);
	        transform: scale(0.9, 1);
	transition: all 200ms ease;
}
.arrow:hover #arrow-body {
	-webkit-transform: scale(1, 1);
	        transform: scale(1, 1);
	transition: all 200ms ease;
}

.up-button {
  display: -webkit-flex;
  display: flex;
  margin: 30px auto 22px auto;
  height: 50px;
  width: 50px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  transition: all 200ms ease;
  cursor: pointer;
}

.up-arrow {
  -webkit-transform: rotate(-90deg) scale(1.4);
          transform: rotate(-90deg) scale(1.4);
}

/* HAMBURGER MENU STYLES */

#nav-icon3 span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  justify-self: right;
  background:white;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}

#nav-icon3 span:nth-child(1) {
  top: 0px;
}

#nav-icon3 span:nth-child(2),#nav-icon3 span:nth-child(3) {
  top: 9px;
}

#nav-icon3 span:nth-child(4) {
  top: 18px;
}

#nav-icon3.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

#nav-icon3.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

#nav-icon3.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#nav-icon3.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

/* LANDING PAGE BUTTON STYLES */

.left-button {
  display: grid; 
  grid-column: 4 / 5; 
  grid-row: 3 / span 1;
  align-self: center; 
  justify-self: end; 
  -webkit-align-content: center; 
          align-content: center;
}
.first-button {
 grid-row: 2 / span 1;
 margin-top: 15px;
}

.first-button > button {
 margin-top: 0;
}

.right-button {
  display: grid; 
  grid-column: 2 / 3; 
  grid-row: 3 / span 1;
  align-self: center; 
  justify-self: start; 
  -webkit-align-content: center; 
          align-content: center
}

.home-button {
  display: grid; 
  grid-column: 4 / 5; 
  grid-row: 3 / span 1;
  align-self: center; 
  justify-self: end; 
  -webkit-align-content: center; 
          align-content: center
}

.right {
  grid-column: 2 / 3; 
}

.page-image { 
  border-radius: 4px; 
}

input:focus, textarea:focus {
  outline: none;
}

.contact {
  grid-column: 2 / 3;
  justify-self: center;
  align-self: center;
  grid-row: 2 / span 1;
}

.contact button {
  margin: 0;
}

.contact-for-more {
  margin: 0 auto; 
  width: 50%;
}

.svg-left-arrow {
  /* -webkit-transform: rotate(180deg);
  transform: rotate(180deg); */
  width: 30px; 
  height: 30px;
}

.obama {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  justify-self: center;
  align-self: center;
  margin: 1% 0 2% 0;
}
.obama button {
  margin: 0;
}

@media (max-width: 1325px) {
  .first-button {
    margin-top: 0px;
  }
}

@media (max-width: 900px) {
  .left-button, .right-button, .home-button {
    grid-column: 2 / 4;
    grid-row: 2 / span 1;
    justify-self: center;
  }

  #contact-home-button {
    grid-column: 2 / 3;
    grid-row: 2 / span 1;
    justify-self: center;
  }

  .contact-for-more {
    margin: 0 auto; 
    width: 100%;
  }
}

